@tailwind base;
@tailwind components;
@tailwind utilities;

/* ---------- Base Styles ---------- */
@layer base {
  body {
    font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 
      'Segoe UI', Roboto, sans-serif;
    background-color: #f8fafc;
    color: #0f172a;
  }
}

/* ---------- Reusable Components ---------- */
@layer components {
  .btn-primary {
    @apply bg-indigo-600 text-white px-4 py-2 rounded-xl hover:bg-indigo-700 transition;
  }

  .btn-secondary {
    @apply bg-slate-200 text-slate-800 px-4 py-2 rounded-xl hover:bg-slate-300 transition;
  }

  .input {
    @apply w-full px-3 py-2 bg-slate-50 border border-slate-200 rounded-xl 
           focus:ring-2 focus:ring-indigo-500 outline-none;
  }

  .card {
    @apply bg-white p-6 rounded-2xl border border-slate-200 shadow-sm;
  }

  .section {
    @apply max-w-4xl mx-auto px-4;
  }

  .fade-in {
    animation: fadeIn 0.3s ease-in-out;
  }

  .slide-in-bottom {
    animation: slideInBottom 0.3s ease-in-out;
  }
}

/* ---------- Animations ---------- */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInBottom {
  from {
    transform: translateY(10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* ---------- Scrollbar ---------- */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}
